@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
::-webkit-scrollbar {
  width: 8px;
  background-color: grey;
}
a {
  text-decoration: inherit;
  color: inherit;
}
.app {
  min-height: 100.5vh;
  border: 10px solid rgb(156, 142, 142);
  margin: 8px;
  padding: 5px;
}

.header {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.link {
  font-size: 8vw;
  letter-spacing: 1px;
  font-family: "Zen Kurenaido", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  cursor: pointer;
}
.divider {
  width: 100%;
  margin: 10px;
  background-color: grey;
}

.content {
  display: flex;
  justify-content: space-around;
  /* flex-direction: column-reverse; */
}
.catg {
  width: 45%;
  display: flex;
  padding: 10px;
  align-items: center;
  flex-direction: column;
  font-weight: 300;
  font-family: "Zen Old Mincho", serif;
}
img {
  /* object-fit: contain; */
  width: 55%;
  align-self: center;
  padding: 4px;
}
.catg_field {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-evenly;
}
@media (max-width: 800px) {
  .content {
    flex-direction: column-reverse;
    align-items: center;
  }
  .catg {
    width: 100%;
    margin-top: 5px;
  }
  img {
    width: 75%;
    margin-top: 20px;
  }
}

.quiz {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.user {
  border: 1px solid black;
  font-size: 30px;
  padding: 15px;
  font-family: sans-serif;
  font-weight: 400;
  text-transform: capitalize;
  box-shadow: 4px 4px 2px black;
}
.status {
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  margin: 10px;

  width: 100%;
}

.question {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.que-box {
  width: 95%;
  min-height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border: 5px solid grey;
  padding: 20px;
  margin-top: 10px;
}
.option {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex: 1 1;
  align-items: center;
  justify-content: space-around;
  margin: 10px;
}
.singleoption {
  width: 46%;
  height: 50px;
  padding: 15px 20px;
  margin: 10px;
  box-shadow: 0 0 2px black;
}
@media (max-width: 614px) {
  .singleoption {
    width: 100%;
  }
}
.select {
  background-color: rgb(7, 207, 0);
  color: white;
  box-shadow: 0 0 1px black;
}
.wrong {
  background-color: rgb(233, 0, 0);
  color: white;
  box-shadow: 0 0 1px black;
}
.controls {
  display: flex;
  /* align-items: center; */
  justify-content: space-around;
  width: 100%;
}

.result {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 60vh;
  text-align: center;
}
.title {
  font-size: 40px;
}

